//
// general.scss
//

/*********************************/
/*         General                */
/*===============================*/
body {
  font-family: $font-family-secondary;
  overflow-x: hidden !important;
  font-size: $font-size-base;
  color: $dark !important;
  letter-spacing: 1.5px !important;
}
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: $font-family-base;
  line-height: 1.5;
  font-weight: 500 !important;
}
.display-1,
.display-2,
.display-3,
.display-4,
.display-5,
.display-6 {
  line-height: 1.2;
}
::selection {
  background: lighten($dark, 5%);
  color: $white;
}
a {
  text-decoration: none !important;
}
p {
  line-height: 1.6;
}
ul li {
  -webkit-margin-start: 0.25rem;
  margin-inline-start: 0.25rem;
}
ul li:first-child {
  -webkit-margin-start: 0px;
  margin-inline-start: 0px;
}

.list-custom {
  padding-left: 0;
  width: 90%;
}

.list-custom li {
  list-style-type: none;
  padding: 0;
  margin-top: 1rem;
}

::placeholder {
  color: $gray-600 !important;
}

.container-hero-cutsom-sentence {
  display: flex;
  flex-direction: column;
}

.container-hero-custom-sentence h4 {
  margin: 0;
  padding: 0;
}

.hero-custom-sentence {
  font-size: 18px;
  color: $white;
  padding: 0;
  margin-bottom: 15px;
}

@media (max-width: 768px) {
  .typewrite-fixed {
    position: absolute;
    left: 0px;
    top: -50px;
    color: #e8d7b8;
    width: 100%;
  }

  /*     Returns   */
  .heading {
    min-height: 40px; /*      */
  }
}

@media (min-width: 769px) {
  .returns-home-word {
    padding-left: 6px;
  }
}

@media (max-width: 768px) {
  .custom-container-audience {
    margin-top: 45px !important;
  }
}

.transition-text {
  display: inline-block;
  transition: all 1.5s ease;
  font-size: 32px; /* Start size */
}

.grow {
  font-size: 48px; /* Bigger */
}

.shrink {
  font-size: 20px; /* Smaller */
}

@media (max-width: 768px) {
  .transition-text {
    display: inline-block;
    transition: all 1.5s ease;
    font-size: 25px; /* Start size */
  }

  .grow {
    font-size: 30px; /* Bigger */
  }

  .shrink {
    font-size: 17px; /* Smaller */
  }

  .custom-container-countdown-aboutus {
    margin-top: 60px !important;
  }
  .custom-container-countdown-aboutus-ourvalues {
    margin-top: 30px;
  }
}

.home-background-image {
  background-image: url("../../assect/images/bg/03.jpg") !important;
}

.aboutus-background-image {
  background-image: url("../../assect/images/bg/aboutus.webp") !important;
}

.features-background-image {
  background-image: url("../../assect/images/bg/features.webp") !important;
  background-position: center !important;
  background-attachment: fixed !important;
}

.contactus-background-image {
  background-image: url("../../assect/images/bg/contactus.webp") !important;
}

.privacy-policy-background-image {
  background-image: url("../../assect/images/bg/privacy-policy.jpg") !important;
}

.policy-list {
  color: $gray-700;
  width: 90%;
}

.policy-list div {
  margin-top: 0;
  padding-top: 0.3rem;
  color: $gray-700;
  margin-left: 0;
  list-style-position: inside; /* or 'outside' for default */
}

@import "react-phone-input-2/lib/style.css";

// Make sure this is loaded after react-phone-input-2/lib/style.css
// Ensure react-phone-input-2 input matches .form-control
.react-tel-input .form-control,
.react-tel-input input,
.custom-phone-input {
  border: 1px solid $gray-200 !important;
  font-size: 16px !important;
  line-height: 26px !important;
  background-color: $white !important;
  border-radius: 8px !important;
  width: 100% !important;
  height: 39.6px !important;
  box-shadow: none !important;
  padding-left: 48px !important; // for the flag
}

.react-tel-input .form-control:focus,
.react-tel-input input:focus,
.custom-phone-input:focus {
  border-color: $primary !important;
  box-shadow: none !important;
}