/*********************************/
/*            Home               */
/*===============================*/
@mixin home-common {
  background-size: cover !important;
  align-self: center;
  position: relative;
  background-position: center center;
}

.bg-home {
  height: 95vh;
  @include home-common();
  @media (max-width: 767px) {
    padding: 150px 0 !important;
    height: auto;
  }
  overflow: hidden;
}

.bg-home-one {
  padding: 160px 0 220px;
  @include home-common();
}

.bg-half-260 {
  padding: 260px 0;
  @include home-common();
  @media (max-width: 768px) {
    padding: 150px 0;
    height: auto;
  }
}
.bg-half-170 {
  padding: 170px 0;
  @include home-common();
  @media (max-width: 767px) {
    padding: 120px 0;
    height: auto;
  }
}

.bg-building-pic {
  padding: 30px 0;
  @include home-common();
}

/*********************************/
/*            Swiper slider      */
/*===============================*/
.swiper-slider-hero {
  .swiper-container,
  .swiper-slide .slide-inner {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .swiper-container {
    .swiper-button-prev,
    .swiper-button-next {
      background: transparent;
      width: 35px;
      height: 35px;
      line-height: 35px;
      margin-top: -30px;
      &:before {
        font-family: "Material Design Icons";
        font-weight: 900;
        color: $white;
      }
      &:hover {
        background: $primary;
        border-color: $primary !important;
      }
    }
    .swiper-button-prev {
      left: 10px;
      border: 1px solid rgba($white, 0.5);
      &:before {
        content: "\f0141";
      }
    }
    .swiper-button-next {
      right: 10px;
      border: 1px solid rgba($white, 0.5);
      &:before {
        content: "\f0142";
      }
    }
  }
  .swiper-pagination-bullet {
    color: $gray-100;
    background: transparent;
  }
  .swiper-pagination-bullet-active {
    color: $white;
  }
  .swiper-container-horizontal > .swiper-pagination-bullets,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    bottom: 45px;
  }
  .swiper-container-horizontal
    > .swiper-pagination-bullets
    .swiper-pagination-bullet {
    margin: 0 13px;
  }
}

/*********************************/
/*           Kenburn Effect      */
/*===============================*/
.zoom-image {
  position: relative;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  overflow: hidden;
}

.image-wrap {
  position: absolute;
  top: -200px;
  bottom: -200px;
  left: -100px;
  right: -100px;
  min-width: 100%;
  width: auto;
  min-height: 100%;
  height: auto;
  overflow: hidden;
  margin: auto;
  animation: 100s ppb_kenburns linear infinite alternate;
  background-position: center;
}

@keyframes move {
  0% {
    transform-origin: bottom;
    transform: scale(1.1);
  }
  100% {
    transform: scale(1.4);
  }
}

@keyframes ppb_kenburns {
  0% {
    transform: scale(1.4) translateY(2%);
  }
  25% {
    transform: scale(1.2) translateY(0);
  }
  50% {
    transform: scale(1.4) translateY(-2%);
  }
  75% {
    transform: scale(1.2) translateY(0);
  }
  100% {
    transform: scale(1.4) translateY(2%);
  }
}
.carousel {
  &.carousel-slider {
    .control-arrow {
      &:hover {
        background-color: transparent !important;
      }
    }
  }
}

.newtons-cradle {
  --uib-size: 28px;
  --uib-speed: 1s;
  --uib-color: #fff;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--uib-size);
  height: var(--uib-size);
  margin-left: auto;
  margin-right: auto;
}

.newtons-cradle__dot {
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
  width: 25%;
  transform-origin: center top;
}

.newtons-cradle__dot::after {
  content: "";
  display: block;
  width: 100%;
  height: 25%;
  border-radius: 50%;
  background-color: var(--uib-color);
}

.newtons-cradle__dot:first-child {
  animation: swing var(--uib-speed) linear infinite;
}

.newtons-cradle__dot:last-child {
  animation: swing2 var(--uib-speed) linear infinite;
}

@keyframes swing {
  0% {
    transform: rotate(0deg);
    animation-timing-function: ease-out;
  }

  25% {
    transform: rotate(70deg);
    animation-timing-function: ease-in;
  }

  50% {
    transform: rotate(0deg);
    animation-timing-function: linear;
  }
}

@keyframes swing2 {
  0% {
    transform: rotate(0deg);
    animation-timing-function: linear;
  }

  50% {
    transform: rotate(0deg);
    animation-timing-function: ease-out;
  }

  75% {
    transform: rotate(-70deg);
    animation-timing-function: ease-in;
  }
}

.grecaptcha-badge {
  visibility: hidden;
}
