//
// _countdown.scss
//

/*********************************/
/*         Countdown             */
/*===============================*/
#countdown {
  .count-down {
    display: inline-block;
    margin: 30px 10px 10px;
    color: $white;
    text-align: center;
    .count-number {
      font-size: 40px;
      line-height: 95px;
    }
    .count-head {
      display: block;
      position: relative;
      transform: translateY(-25px);
      text-transform: uppercase;
      font-size: 14px;
      letter-spacing: 1px;
    }
  }
}

#countdown {
  .count-down {
    .count-number {
      font-size: 40px;
      line-height: 110px;
      height: 130px;
      width: 130px;
      border-radius: 0.5rem;
      backdrop-filter: blur(5px);
      box-shadow: $shadow-lg;
      background-color: rgba($white, 0.1);
    }
  }
}

#nav-countdown {
  .count-down {
    display: inline-block;
    margin: 5px 0px 0px;
    color: $white;
    text-align: center;
    .count-number {
      font-size: 12px;
      margin-top: 5px;
    }
    .count-head {
      color: #000;
      display: block;
      position: relative;
      transform: translateY(1px);
      text-transform: uppercase;
      font-size: 8px;
      letter-spacing: 1px;
    }
  }
}

#nav-countdown {
  .count-down {
    .count-number {
      color: #000;
      font-size: 12px;
      line-height: 15px;
      height: 35px;
      width: 40px;
      box-shadow: $shadow;
      background-color: rgba($white, 0.1);
    }
  }
}

/*********************************/
/*         Maintenance           */
/*===============================*/
#maintenance {
  color: $white;
  font-size: 56px;
  letter-spacing: 1px;

  .indicator {
    font-size: 18px;
  }
}
