//
// _features.scss
//

/*********************************/
/*         Features              */
/*===============================*/
.features {
  .title {
    &:hover {
      color: $primary !important;
    }
  }
  .feature-icon {
    .hexagon {
      color: transparent !important;
      fill: rgba($primary, 0.05) !important;
      height: 102px;
      width: 102px;
    }
  }
}

/*********************************/
/*         Property Listing      */
/*===============================*/
.property {
  transition: all 0.5s ease;
  .property-image {
    img,
    .property-icon {
      transition: all 0.5s ease;
      li {
        position: relative;
        margin-left: 0;
      }
    }
    .property-icon {
      position: absolute;
      top: 15px;
      right: 15px;
      opacity: 0;
    }
  }
  &:hover {
    box-shadow: $shadow-md !important;
    img {
      transform: scale(1.05);
    }
    .property-icon {
      opacity: 1;
    }
  }
  .title {
    &:hover {
      color: $primary !important;
    }
  }

  &.property-list {
    .property-image {
      img {
        object-fit: cover !important;

        @media (min-width: 768px) {
          width: 12rem !important;
        }
      }
      &.md-full {
        img {
          @media (max-width: 768px) {
            width: 100% !important;
          }
        }
      }
    }
  }
}

.categories {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: all 0.5s ease;

  img {
    width: 100%;
    object-fit: cover;
  }

  .p-3 {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .title {
    &:hover {
      color: $primary !important;
    }
  }

  &:hover {
    box-shadow: $shadow-md !important;
  }
}

@media (max-width: 768px) {
  .my-custom-font {
    font-size: 17px !important;
  }
}

.typewrite {
  color: #e8d7b8 !important;
}
