.timeline-area {
  margin-bottom: 90px;
  margin-top: 30px;
}

.section-heading .button {
  text-align: right;
  margin: auto 0 auto auto;
}

.section-heading .button a {
  font-size: 16px;
  line-height: 24px;
  padding: 16px 32px;
  color: #1a1a1a;
  background-color: #333;
}

.timeline-area .slick-list {
  max-height: 576px;
  height: 100%;
}

.timeline-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding-left: 0;
}

.timeline-item .icon {
  -webkit-box-flex: wrap;
  -ms-flex: wrap;
  flex: wrap;
  cursor: pointer;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  position: relative;
  margin-right: 50px;
}

