.timeline-item .h2 {
  display: block;
  font-size: 52px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.timeline-item img {
  height: 100%;
  object-fit: cover;
  max-height: 144px;
}

.timeline-item img.ishow,
.slick-current img.ifade {
  display: none;
}

.timeline-item img.ifade,
.timeline-item.slick-current img.ishow {
  display: block;
}

.slick-current .icon:before {
  color: #333;
}

.timeline-item .icon.h2 {
  color: #00000025;
  margin-left: 35px;
}

@media (max-width: 1300px) {
  .dash {
    display: none !important;
  }
}

.slick-current .icon.h2 {
  color: #000;
}

.light-mode .section-heading .button a {
  background-color: #1a1a1a;
  color: white;
}

.light-mode .timeline-item .icon.h2 {
  text-shadow: 2px 0 #666;
}

.light-mode .timeline-item.slick-current .icon.h2 {
  color: #1a1a1a;
  text-shadow: 2px 0 #1a1a1a;
}

@media (max-width: 991px) {
  .light-mode .timeline-item.slick-current .icon.h2 {
    text-shadow: unset;
  }
}

.slick-current:before {
  left: 0;
  opacity: 1;
}

.timeline-content .slick-slide {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.timeline-area .timeline-images .content {
  background: #094834;
  padding: 64px;
  height: fit-content;
  width: 39%;
  position: absolute;
  z-index: 1;
  border-radius: 1rem;
  top: 32px;
}

.timeline-area .timeline-images img {
  position: relative;
  float: right;
  margin-left: auto;
  width: 100%;
  max-width: 930px;
  height: 576px;
  object-fit: cover;
  border-radius: 1rem;
}

.timeline-images .description {
  margin-top: 16px;
  margin-bottom: 0;
  color: #fcf7ed;
}

.timeline-images .slick-current .content .h5 {
  color: #d3c294;
  font-weight: 900;
}

@media only screen and (max-width: 1399px) {
  .timeline-area .timeline-item.slick-current .icon:after {
    top: 30px;
    left: 74px;
  }
  .timeline-area .timeline-images img {
    width: 90%;
  }
}

@media only screen and (max-width: 991px) {
  .timeline-area .timeline-images .content {
    padding: 32px;
  }
  .timeline-content {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .timeline-area .timeline-item img {
    height: 64px;
  }

  .timeline-area .slick-list {
    margin-bottom: 64px;
  }

  .timeline-area .slick-slide .description {
    max-height: 400px;
  }

  .timeline-area .timeline-images img {
    width: 75%;
  }

  .timeline-area .timeline-images .content {
    width: 50%;
  }

  .timeline-area .timeline-images .content {
    top: 64px;
  }
  .timeline-content {
    display: none;
  }
  .timeline-area {
    margin-bottom: 0px;
    margin-top: 0px;
  }
}

@media only screen and (max-width: 576px) {
  .timeline-area .section-title {
    text-align: center;
    margin-bottom: 32px;
  }

  .timeline-area .button {
    text-align: center;
  }

  .timeline-area .timeline-images .content {
    position: relative;
    width: 100%;
  }

  .timeline-area .timeline-images img {
    width: 100%;
    height: 320px;
    object-fit: cover;
  }

  .timeline-area .slick-list {
    margin-bottom: 0;
  }
}

.dash {
  width: 50px;
  height: 2px;
  background-color: #000;
  display: block;
  position: absolute;
  top: 50%;
  left: 150%;
}
