.philosophy-card {
  max-width: 900px;
  margin: 40px auto;
  padding: 60px;
  background-color: #ffffff;
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.05);
  box-sizing: border-box;
  background: url("../../assect//images/shiro-bg.jpg");
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.card-title {
  font-size: 22px;
  font-weight: 600;
  color: #333;
}

.card-text {
  font-size: 17px;
  color: #555;
  line-height: 1.7;
}

/* Responsive */
@media (max-width: 768px) {
  .philosophy-card {
    padding: 20px 18px;
  }

  .card-title {
    font-size: 20px;
  }

  .card-text {
    font-size: 15px;
  }

  .card-icon {
    font-size: 20px;
  }
}
