.quote-container {
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px 20px;
  border-radius: 16px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  text-align: center;
  box-sizing: border-box;
  background: url("../../assect//images/shiro-bg.jpg");
  margin-top: 60px;
}

.quote-symbol {
  font-size: 48px;
  color: #444;
  /* margin-bottom: 10px; */
}

.quote-heading {
  font-size: 24px;
  font-weight: 600;
  color: #222;
  margin-bottom: 20px;
}

.quote-paragraph {
  font-size: 16px;
  color: #555;
  line-height: 1.7;
}

/* Responsive */
@media (max-width: 768px) {
  .quote-container {
    padding: 20px 15px;
  }

  .quote-heading {
    font-size: 20px;
  }

  .quote-paragraph {
    font-size: 15px;
  }
}
